import React from 'react'
import Header from '../Header';
import Sidebar from '../Sidebar';
import Customize from './Customize';

const Notification = () => {
    return (
        <main className="main" id="top">
          <div className="container-fluid" data-layout="container">
            <Header />
    
            <div className="content">
              <Sidebar />
              <div className="card mt-2 mb-3">
              <div className="card overflow-hidden">
            <div className="card-header bg-body-tertiary">
              <div className="row flex-between-center">
                <div className="col-sm-auto">
                  <h5 className="mb-1 mb-md-0">Your Notifications</h5>
                </div>
                <div className="col-sm-auto fs--1"><a className="font-sans-serif" href="#!">Mark all as read</a><a className="font-sans-serif ms-2 ms-sm-3" href="#notification-settings-modal"  data-bs-toggle="modal"
                                    data-bs-target="#staticBackdrop">Notification settings</a></div>
              </div>
            </div>
            <div className="card-body fs--1 p-0">
              <a className="border-bottom-0 notification rounded-0 border-x-0 border-300" href="#!">
                <div className="notification-avatar">
                  <div className="avatar avatar-xl me-3">
                    <img className="rounded-circle" src="../../assets/img/team/1.jpg" alt="" />
                  </div>
                </div>
                <div className="notification-body">
                  <p className="mb-1">Announcing the winners of the <strong>The only book awards</strong> decided by you, the readers. Check out the champions and runners-up in all 21 categories now!</p>
                  <span className="notification-time"><span className="me-2" role="img" aria-label="Emoji">📢</span>Just Now</span>
                </div>
              </a>
              <a className="border-bottom-0 notification-unread notification rounded-0 border-x-0 border-300" href="#!">
                <div className="notification-avatar">
                  <div className="avatar avatar-xl me-3">
                    <img className="rounded-circle" src="../../assets/img/team/2.jpg" alt="" />
                  </div>
                </div>
                <div className="notification-body">
                  <p className="mb-1">Last chance to vote in <strong>The 2018 Falcon Choice Awards</strong>! See what made it to the Final Round and help your favorites take home the win. Voting closes on November 26</p>
                  <span className="notification-time"><span className="me-2" role="img" aria-label="Emoji">🏆</span>15m</span>
                </div>
              </a>
              <a className="border-bottom-0 notification rounded-0 border-x-0 border-300" href="#!">
                <div className="notification-avatar">
                  <div className="avatar avatar-xl me-3">
                    <img className="rounded-circle" src="../../assets/img/team/3.jpg" alt="" />
                  </div>
                </div>
                <div className="notification-body">
                  <p className="mb-1"><strong>Jennifer Kent</strong> declared you as a <strong>President</strong> of Computer Science and Engineering Society</p>
                  <span className="notification-time"><span className="me-2" role="img" aria-label="Emoji">📢</span>1h</span>
                </div>
              </a>
              <a className="border-bottom-0 notification-unread notification rounded-0 border-x-0 border-300" href="#!">
                <div className="notification-avatar">
                  <div className="avatar avatar-xl me-3">
                    <img className="rounded-circle" src="../../assets/img/team/4.jpg" alt="" />
                  </div>
                </div>
                <div className="notification-body">
                  <p className="mb-1">Congratulate <strong>Woody Allen</strong> for starting a new position as Business Development Manager &amp; Implementation Engineer at <strong>Hewlett Packard Enterprise(HP)</strong></p>
                  <span className="notification-time"><span className="me-2" role="img" aria-label="Emoji">🎁</span>6h</span>
                </div>
              </a>
              <a className="border-bottom-0 notification-unread notification rounded-0 border-x-0 border-300" href="#!">
                <div className="notification-avatar">
                  <div className="avatar avatar-xl me-3">
                    <img className="rounded-circle" src="../../assets/img/team/5.jpg" alt="" />
                  </div>
                </div>
                <div className="notification-body">
                  <p className="mb-1"><strong>Christopher Nolan</strong> Mentioned you in a comment : "Congratulations! Wishing you a great future. Proud fo you ❤️"</p>
                  <span className="notification-time"><span className="me-2" role="img" aria-label="Emoji">💬</span>8h</span>
                </div>
              </a>
              <a className="border-bottom-0 notification rounded-0 border-x-0 border-300" href="#!">
                <div className="notification-avatar">
                  <div className="avatar avatar-xl me-3">
                    <img className="rounded-circle" src="../../assets/img/logos/harvard.png" alt="" />
                  </div>
                </div>
                <div className="notification-body">
                  <p className="mb-1">Join <strong>GIS Institute Winter 2019</strong> Event arranged by <strong>Harvard University</strong>. Only three days to go.</p>
                  <span className="notification-time"><span className="me-2" role="img" aria-label="Emoji">🚶</span>9h</span>
                </div>
              </a>
              <a className="border-bottom-0 notification-unread notification rounded-0 border-x-0 border-300" href="#!">
                <div className="notification-avatar">
                  <div className="avatar avatar-xl me-3">
                    <img className="rounded-circle" src="../../assets/img/team/7.jpg" alt="" />
                  </div>
                </div>
                <div className="notification-body">
                  <p className="mb-1"><strong>Peter Jackson</strong> set CSE Curnival Programe in New Museums Site in <strong>University of Cambridge</strong></p>
                  <span className="notification-time"><span className="me-2" role="img" aria-label="Emoji">💻</span>14h</span>
                </div>
              </a>
              <a className="border-bottom-0 notification rounded-0 border-x-0 border-300" href="#!">
                <div className="notification-avatar">
                  <div className="avatar avatar-xl me-3">
                    <img className="rounded-circle" src="../../assets/img/team/8.jpg" alt="" />
                  </div>
                </div>
                <div className="notification-body">
                  <p className="mb-1"><strong>David Fincher</strong> declared you as a verified member of <strong>Stanford University.</strong></p>
                  <span className="notification-time"><span className="me-2" role="img" aria-label="Emoji">✔️</span>2d</span>
                </div>
              </a>
              <a className="border-bottom-0 notification rounded-0 border-x-0 border-300" href="#!">
                <div className="notification-avatar">
                  <div className="avatar avatar-xl me-3">
                    <img className="rounded-circle" src="../../assets/img/team/9.jpg" alt="" />
                  </div>
                </div>
                <div className="notification-body">
                  <p className="mb-1">Follow <strong>International Conference on Water and Environmental Engineering (ICWEE)</strong> event arranged by United Nations Development Program on 30 December 2018</p>
                  <span className="notification-time"><span className="me-2" role="img" aria-label="Emoji">🔍</span>3d</span>
                </div>
              </a>
              <a className="border-bottom-0 notification rounded-0 border-x-0 border-300" href="#!">
                <div className="notification-avatar">
                  <div className="avatar avatar-xl me-3">
                    <img className="rounded-circle" src="../../assets/img/team/10.jpg" alt="" />
                  </div>
                </div>
                <div className="notification-body">
                  <p className="mb-1"><strong>James Cameron</strong> invited to join the group: United Nations International Children's Fund</p>
                  <span className="notification-time"><span className="me-2" role="img" aria-label="Emoji">🙋‍</span>2d</span>
                </div>
              </a>
              <a className="border-bottom-0 notification-unread  notification rounded-0 border-x-0 border-300" href="#!">
                <div className="notification-avatar">
                  <div className="avatar avatar-xl me-3">
                    <img className="rounded-circle" src="../../assets/img/logos/oxford.png" alt="" />
                  </div>
                </div>
                <div className="notification-body">
                  <p className="mb-1"><strong>University of Oxford</strong> created an event : "Causal Inference Hilary 2019"</p>
                  <span className="notification-time"><span className="me-2" role="img" aria-label="Emoji">✌️</span>1w</span>
                </div>
              </a>
            </div>
          </div>
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
          
    
    
    
    
          <div
                  className="modal fade"
                  id="staticBackdrop"
                  data-bs-keyboard="false"
                  data-bs-backdrop="static"
                  tabIndex={-1}
                  aria-labelledby="staticBackdropLabel"
                  aria-hidden="true"
                  style={{ display: "none" }}
                >
                  <div className="modal-dialog modal-lg mt-6" role="document">
                  <div className="modal-content">
                <div className="modal-header d-flex align-items-center border-bottom px-4">
                  <h4 className="mb-0" id="notification-settings-modal-label">Notification Settings</h4>
                </div>
                <div className="modal-body p-4">
                  <form className="text-start">
                    <div className="form-check lh-1 mb-0"><input className="form-check-input" type="radio" defaultValue id="always" name="notification-settings" defaultChecked="checked" /><label className="form-check-label mb-0" htmlFor="always">Get a notification each time there is activity on your page or an important update.</label></div>
                    <div className="form-check lh-1 mb-0"><input className="form-check-input" type="radio" defaultValue id="everyday" name="notification-settings" /><label className="form-check-label mb-0" htmlFor="everyday">Get one notification every 12-24 hours on all activity and updates.</label></div>
                    <div className="form-check lh-1 mb-0"><input className="form-check-input" type="radio" defaultValue id="off" name="notification-settings" /><label className="form-check-label mb-0" htmlFor="off">Off</label></div>
                    <h5 className="fs-0 mb-3 mt-4">Edit your notification settings for: </h5>
                    <ul className="list-group list-group-flush mb-4 fs--1">
                      <li className="list-group-item d-flex justify-content-between align-items-center py-2 px-0 border-200 bg-transparent"><span>New Mention of Page </span><span className="form-check fs-0 mb-0"><input className="form-check-input me-0" id="checkbox1" type="checkbox" name="checkbox1" defaultChecked="checked" /></span></li>
                      <li className="list-group-item d-flex justify-content-between align-items-center py-2 px-0 border-200 bg-transparent"><span>New Comments on page post</span><span className="form-check fs-0 mb-0"><input className="form-check-input me-0" id="checkbox2" type="checkbox" name="checkbox2" defaultChecked="checked" /></span></li>
                      <li className="list-group-item d-flex justify-content-between align-items-center py-2 px-0 border-200 bg-transparent"><span>Edits to Comments you have written</span><span className="form-check fs-0 mb-0"><input className="form-check-input me-0" id="checkbox3" type="checkbox" name="checkbox3" defaultChecked="checked" /></span></li>
                    </ul>
                    <div className="form-check mb-0"><input className="form-check-input" type="checkbox" id="followers-activity" /><label className="form-check-label mb-0" htmlFor="followers-activity">Allow notifications from your followers activity</label></div>
                    <div className="form-check mb-0"><input className="form-check-input" type="checkbox" id="groups" /><label className="form-check-label mb-0" htmlFor="groups">Groups</label></div>
                    <div className="form-check mb-0"><input className="form-check-input" type="checkbox" id="associations" /><label className="form-check-label mb-0" htmlFor="associations">Associations</label></div>
                  </form>
                </div>
                <div className="modal-footer border-top text-start py-3 px-4 d-flex justify-content-end"><button className="btn btn-secondary btn-sm" data-bs-dismiss="modal">Cancel</button><button className="btn btn-primary btn-sm ms-2" type="submit">Update</button></div>
              </div>
                  </div>
                </div>
    
    
    
    
    
    
        
              </div>
    
              
    
    
            </div>
          </div>

          <Customize/>
        </main>
      );
}

export default Notification
