import React, { useState, useEffect } from 'react';

const Drekanddrop = () => {
    const [rows, setRows] = useState(() => {
        const storedRows = localStorage.getItem('tableRows');
        return storedRows ? JSON.parse(storedRows) : [
            { id: 1, name: 'John Doe', age: 25, country: 'USA' },
            { id: 2, name: 'Jane Doe', age: 30, country: 'Canada' },
            // Add more rows as needed
        ];
    });

    useEffect(() => {
        localStorage.setItem('tableRows', JSON.stringify(rows));
    }, [rows]);

    const handleDragStart = (e, index) => {
        e.dataTransfer.effectAllowed = 'move';
        e.dataTransfer.setData('text/html', index);
    };

    const handleDragOver = (e) => {
        e.preventDefault();
    };

    const handleDrop = (e, index) => {
        const dragIndex = parseInt(e.dataTransfer.getData('text/html'), 10);
        const draggedRow = rows[dragIndex];
        const newRows = [...rows];

        newRows.splice(dragIndex, 1);
        newRows.splice(index, 0, draggedRow);

        setRows(newRows);
    };

    const handleDragEnd = () => {
        setRows((prevRows) => {
            return prevRows.map((row, index) => ({ ...row, id: index + 1 }));
        });
    };

    return (
        <div>
            <table>
                <thead>
                    <tr>
                        <th>#</th>
                        <th>Name</th>
                        <th>Age</th>
                        <th>Country</th>
                    </tr>
                </thead>
                <tbody>
                    {rows.map((row, index) => (
                        <tr
                            key={row.id}
                            draggable
                            onDragStart={(e) => handleDragStart(e, index)}
                            onDragOver={handleDragOver}
                            onDrop={(e) => handleDrop(e, index)}
                            onDragEnd={handleDragEnd}
                        >
                            <td>{index + 1}</td>
                            <td>{row.name}</td>
                            <td>{row.age}</td>
                            <td>{row.country}</td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
};

export default Drekanddrop;
