import { useState } from 'react';
import { DndProvider, useDrag, useDrop } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';

const DraggableItem = ({ id, text, index, moveItem }) => {
    const [, drag] = useDrag({
        type: 'ITEM',
        item: { id, index },
    });

    return (
        <div ref={drag} style={{ border: '1px solid #ccc', padding: '8px', marginBottom: '8px' }}>
            {text}
        </div>
    );
};

const DropZone = ({ items, moveItem }) => {
    const [, drop] = useDrop({
        accept: 'ITEM',
        hover: (draggedItem) => {
            if (draggedItem.index !== undefined) {
                moveItem(draggedItem.index, items.length - 1);
            }
        },
    });

    return (
        <div
            ref={drop}
            style={{
                minHeight: '100px',
                border: '1px dashed #ccc',
                padding: '8px',
                position: 'relative', // Ensure the container has a relative position
            }}
        >
            {items.map((item, index) => (
                <DraggableItem key={item.id} id={item.id} text={item.text} index={index} moveItem={moveItem} />
            ))}
        </div>
    );
};

const DragAndDropTable = () => {
    const [items, setItems] = useState([
        { id: 1, text: 'Item 1' },
        { id: 2, text: 'Item 2' },
        { id: 3, text: 'Item 3' },
    ]);

    const moveItem = (startIndex, endIndex) => {
        const updatedItems = [...items];
        const [removedItem] = updatedItems.splice(startIndex, 1);
        updatedItems.splice(endIndex, 0, removedItem);
        setItems(updatedItems);
    };

    return (
        <div style={{ position: 'fixed', top: 0, left: 0, right: 0, bottom: 0, overflow: 'hidden' }}>
            <DndProvider backend={HTML5Backend}>
                <DropZone items={items} moveItem={moveItem} />
            </DndProvider>
        </div>
    );
};

export default DragAndDropTable;
