import React from 'react'
import Sidebar from '../Sidebar'
import Header from '../Header'
import Customize from './Customize'

const AddEmployee = () => {
    return (
        <main className="main " id="top">
            <div className="container-fluid" data-layout="container">

                <Header />

                <div className='content'>
                    <Sidebar />
                    <div className="card mt-3 mb-3">
                        <div className="card-header">
                            <div className="row flex-between-end">
                                <div className="col-auto align-self-center">
                                    <h5
                                        className="mb-0"
                                        data-anchor="data-anchor"
                                        id="readonly-plain-text"
                                    >
                                       Add Employee
                                        <a
                                            className="anchorjs-link "
                                            aria-label="Anchor"
                                            data-anchorjs-icon="#"
                                            href="#readonly-plain-text"
                                            style={{ paddingLeft: "0.375em" }}
                                        />
                                    </h5>
                                </div>
                            </div>
                        </div>
                        <div className="card-body bg-body-tertiary">
                            <div className="tab-content">
                                <div
                                    className="tab-pane preview-tab-pane active"
                                    role="tabpanel"
                                    aria-labelledby="tab-dom-a9ac3157-24ac-47be-a236-744daaa16c16"
                                    id="dom-a9ac3157-24ac-47be-a236-744daaa16c16"
                                >
                                    <div className=" mb-3 row">
                                        <label
                                            className="col-sm-3 col-form-label"
                                            htmlFor="employeeName"
                                        >
                                            Employee Name<code>*</code>
                                        </label>
                                        <div className="col-sm-3">
                                            <input
                                                id="employeeName"
                                                className="form-control"
                                                type="text"
                                            />
                                        </div>
                                    </div>

                                    <div className=" mb-3 row">
                                        <label
                                            className="col-sm-3 col-form-label"
                                            htmlFor="taskDescription"
                                        >
                                            Task Description<code>*</code>
                                        </label>
                                        <div className="col-sm-9">
                                            <textarea id="taskDescription" className="form-control" />
                                        </div>
                                    </div>

                                    <div className="mb-3 row">
                                        <label
                                            className="col-sm-3 col-form-label"
                                            htmlFor="ticketCode"
                                        >
                                            Ticket Code<code>*</code>
                                        </label>
                                        <div className="col-sm-3">
                                            <input
                                                id="ticketCode"
                                                className="form-control"
                                                type="text"
                                            />
                                        </div>

                                        <label
                                            className="col-sm-3 col-form-label"
                                            htmlFor="verificationCode"
                                        >
                                            Verification Code<code>*</code>
                                        </label>
                                        <div className="col-sm-2">
                                            <input
                                                id="verificationCode"
                                                className="form-control"
                                                type="text"
                                                placeholder="NA"
                                            />
                                        </div>
                                    </div>

                                    <div className="mb-3 row">
                                        <label
                                            className="col-sm-3 col-form-label"
                                            htmlFor="developer1"
                                        >
                                            Developer 1<code>*</code>
                                        </label>
                                        <div className="col-sm-3">
                                            <input
                                                id="developer1"
                                                className="form-control"
                                                type="text"
                                            />
                                        </div>

                                        <label
                                            className="col-sm-3 col-form-label"
                                            htmlFor="developer2"
                                        >
                                            Developer 2<code>*</code>
                                        </label>
                                        <div className="col-sm-3">
                                            <input
                                                id="developer2"
                                                className="form-control"
                                                type="text"
                                            />
                                        </div>
                                    </div>

                                    <div className="mb-3 row">
                                        <label className="col-sm-3 col-form-label" htmlFor="qa1">
                                            QA 1<code>*</code>
                                        </label>
                                        <div className="col-sm-3">
                                            <input id="qa1" className="form-control" type="text" />
                                        </div>

                                        <label className="col-sm-3 col-form-label" htmlFor="qa2">
                                            QA 2<code>*</code>
                                        </label>
                                        <div className="col-sm-3">
                                            <input id="qa2" className="form-control" type="text" />
                                        </div>
                                    </div>

                                    <div className='text-center'>
                                        <button className='btn btn-primary '>Add Employee</button>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
            <Customize/>
        </main>
    )
}

export default AddEmployee
