import React from "react";
import Sidebar from "../Sidebar";
import Header from "../Header";
import swal from 'sweetalert';
const Add_Task = () => {

  let addtask=()=>{
    swal({
      title: "Are you sure?",
      text: "Add Task successfully ?",
      icon: "success",
      dangerMode: true,
      closeOnCancel:true,
    })
  }
  return (
    <main className="main" id="top">
      <div className="container-fluid" data-layout="container">
        <Header />

        <div className="content">
          <Sidebar />
          <div className="card mb-3">
            <div className="card-header">
              <div className="row flex-between-end">
                <div className="col-auto align-self-center">
                  <h5
                    className="mb-0"
                    data-anchor="data-anchor"
                    id="readonly-plain-text"
                  >
                    Task Description
                    <a
                      className="anchorjs-link "
                      aria-label="Anchor"
                      data-anchorjs-icon="#"
                      href="#readonly-plain-text"
                      style={{ paddingLeft: "0.375em" }}
                    />
                  </h5>
                </div>
              </div>
            </div>
            <div className="card-body bg-body-tertiary">
              <div className="tab-content">
                <div
                  className="tab-pane preview-tab-pane active"
                  role="tabpanel"
                  aria-labelledby="tab-dom-a9ac3157-24ac-47be-a236-744daaa16c16"
                  id="dom-a9ac3157-24ac-47be-a236-744daaa16c16"
                >
                  <div className="mb-3 row">
                    <label
                      className="col-sm-3 col-form-label"
                      htmlFor="businessArea"
                    >
                      Business Area<code>*</code>
                    </label>
                    <div className="col-sm-3">
                      <select
                        id="businessArea"
                        className="form-select"
                        aria-label="Business Area"
                      >
                        <option selected>Open this select menu</option>
                        <option value={1}>One</option>
                        <option value={2}>Two</option>
                        <option value={3}>Three</option>
                      </select>
                    </div>

                    <label
                      className="col-sm-3 col-form-label"
                      htmlFor="employeeName"
                    >
                      Employee Name<code>*</code>
                    </label>
                    <div className="col-sm-3">
                      <input
                        id="employeeName"
                        className="form-control"
                        type="text"
                      />
                    </div>
                  </div>

                  <div className="mb-3 row">
                    <label
                      className="col-sm-3 col-form-label"
                      htmlFor="taskType"
                    >
                      Task Type<code>*</code>
                    </label>
                    <div className="col-sm-3">
                      <select
                        id="taskType"
                        className="form-select"
                        aria-label="Task Type"
                      >
                        <option selected>select this one</option>
                        <option value={1}>One</option>
                        <option value={2}>Two</option>
                        <option value={3}>Three</option>
                      </select>
                    </div>

                    <label
                      className="col-sm-3 col-form-label"
                      htmlFor="taskStatus"
                    >
                      Task Status<code>*</code>
                    </label>
                    <div className="col-sm-3">
                      <select
                        id="taskStatus"
                        className="form-select"
                        aria-label="Task Status"
                      >
                        <option selected>Pending</option>
                      </select>
                    </div>
                  </div>

                  <div className="mb-3 row">
                    <label
                      className="col-sm-3 col-form-label"
                      htmlFor="project"
                    >
                      Project<code>*</code>
                    </label>
                    <div className="col-sm-3">
                      <select
                        id="project"
                        className="form-select"
                        aria-label="Project"
                      >
                        <option selected>select project</option>
                        <option value={1}>One</option>
                        <option value={2}>Two</option>
                        <option value={3}>Three</option>
                      </select>
                    </div>
                  </div>

                  {/* ... (similar changes for other fields) */}

                  <div className="mb-3 row">
                    <label
                      className="col-sm-3 col-form-label"
                      htmlFor="taskName"
                    >
                      Task Name<code>*</code>
                    </label>
                    <div className="col-sm-9">
                      <input
                        id="taskName"
                        className="form-control"
                        type="text"
                      />
                    </div>
                  </div>

                  <div className="mb-3 row">
                    <label
                      className="col-sm-3 col-form-label"
                      htmlFor="taskDescription"
                    >
                      Task Description<code>*</code>
                    </label>
                    <div className="col-sm-9">
                      <textarea id="taskDescription" className="form-control" />
                    </div>
                  </div>

                  <div className="mb-3 row">
                    <label
                      className="col-sm-3 col-form-label"
                      htmlFor="ticketCode"
                    >
                      Ticket Code<code>*</code>
                    </label>
                    <div className="col-sm-3">
                      <input
                        id="ticketCode"
                        className="form-control"
                        type="text"
                      />
                    </div>

                    <label
                      className="col-sm-3 col-form-label"
                      htmlFor="verificationCode"
                    >
                      Verification Code<code>*</code>
                    </label>
                    <div className="col-sm-2">
                      <input
                        id="verificationCode"
                        className="form-control"
                        type="text"
                        placeholder="NA"
                      />
                    </div>
                  </div>

                  <div className="mb-3 row">
                    <label
                      className="col-sm-3 col-form-label"
                      htmlFor="developer1"
                    >
                      Developer 1<code>*</code>
                    </label>
                    <div className="col-sm-3">
                      <input
                        id="developer1"
                        className="form-control"
                        type="text"
                      />
                    </div>

                    <label
                      className="col-sm-3 col-form-label"
                      htmlFor="developer2"
                    >
                      Developer 2<code>*</code>
                    </label>
                    <div className="col-sm-3">
                      <input
                        id="developer2"
                        className="form-control"
                        type="text"
                      />
                    </div>
                  </div>

                  <div className="mb-3 row">
                    <label className="col-sm-3 col-form-label" htmlFor="qa1">
                      QA 1<code>*</code>
                    </label>
                    <div className="col-sm-3">
                      <input id="qa1" className="form-control" type="text" />
                    </div>

                    <label className="col-sm-3 col-form-label" htmlFor="qa2">
                      QA 2<code>*</code>
                    </label>
                    <div className="col-sm-3">
                      <input id="qa2" className="form-control" type="text" />
                    </div>
                  </div>

                  
                </div>
              </div>
            </div>
          </div>
          <div className="card mb-3">
            <div className="card-header">
              <div className="row flex-between-end">
                <div className="col-auto align-self-center">
                  <h5
                    className="mb-0"
                    data-anchor="data-anchor"
                    id="readonly-plain-text"
                  >
                    Task Timeline
                    <a
                      className="anchorjs-link "
                      aria-label="Anchor"
                      data-anchorjs-icon="#"
                      href="#readonly-plain-text"
                      style={{ paddingLeft: "0.375em" }}
                    />
                  </h5>
                </div>
              </div>
            </div>
            <div className="card-body bg-body-tertiary">
              <div className="tab-content">
                <div
                  className="tab-pane preview-tab-pane active"
                  role="tabpanel"
                  aria-labelledby="tab-dom-a9ac3157-24ac-47be-a236-744daaa16c16"
                  id="dom-a9ac3157-24ac-47be-a236-744daaa16c16"
                >
                  <div className="mb-3 row">
                    <label className="col-sm-3 col-form-label" htmlFor="shift">
                      Shift<code>*</code>
                    </label>
                    <div className="col-sm-3">
                      <select
                        id="shift"
                        className="form-select"
                        aria-label="Shift"
                      >
                        <option selected>Choose Shift</option>
                        <option value={1}>Morning</option>
                        <option value={2}>Afternoon</option>
                        <option value={3}>Night</option>
                      </select>
                    </div>
                  </div>

                  <div className="mb-3 row">
                    <label
                      className="col-sm-3 col-form-label"
                      htmlFor="shiftStart"
                    >
                      Shift Start Time<code>*</code>
                    </label>
                    <div className="col-sm-3">
                      <input
                        id="shiftStart"
                        className="form-control"
                        type="text"
                        placeholder="HH:mm AM/PM"
                      />
                    </div>
                    <label
                      className="col-sm-1 col-form-label"
                      htmlFor="shiftEnd"
                    >
                      /
                    </label>
                    <div className="col-sm-3">
                      <input
                        id="shiftEnd"
                        className="form-control"
                        type="text"
                        placeholder="HH:mm AM/PM"
                      />
                    </div>
                  </div>

                  <div className="mb-3 row">
                    <label
                      className="col-sm-3 col-form-label"
                      htmlFor="priority"
                    >
                      Priority<code>*</code>
                    </label>
                    <div className="col-sm-3">
                      <input
                        id="priority"
                        placeholder="0"
                        className="form-control"
                        type="text"
                      />
                    </div>
                  </div>

                  <div className="mb-3 row">
                    <label
                      className="col-sm-3 col-form-label"
                      htmlFor="startDate"
                    >
                      Start Date<code>*</code>
                    </label>
                    <div className="col-sm-3">
                      <input
                        id="startDate"
                        className="form-control"
                        type="date"
                      />
                    </div>

                    <label
                      className="col-sm-1 col-form-label"
                      htmlFor="endDate"
                    >
                      /
                    </label>

                    <div className="col-sm-3">
                      <input
                        id="endDate"
                        className="form-control"
                        type="date"
                      />
                    </div>
                  </div>

                  <div className="mb-3 row">
                    <label
                      className="col-sm-3 col-form-label"
                      htmlFor="timeRequired"
                    >
                      Time Required (In Hr)<code>*</code>
                    </label>
                    <div className="col-sm-3">
                      <input
                        placeholder="10"
                        id="timeRequired"
                        className="form-control"
                        type="text"
                      />
                    </div>
                  </div>

                  <div className="mb-3 row">
                    <label className="col-sm-3 col-form-label" htmlFor="hours">
                      Hours<code>*</code>
                    </label>
                    <div className="col-sm-3">
                      <input
                        placeholder="10"
                        id="hours"
                        className="form-control"
                        type="text"
                      />
                    </div>
                  </div>

                  <div className="text-center"> 
                    <button onClick={addtask} className="btn btn-primary">Add Task</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
  );
};

export default Add_Task;
