import React from 'react'
import Header from '../Header'
import Sidebar from '../Sidebar'
import Customize from './Customize'

const Changepassword = () => {
    return (
        <main className="main" id="top">
            <div className="container-fluid" data-layout="container">

                <Header />

                <div className='content'>
                    <Sidebar />

                    <div className="card mb-3" style={{ height: "600px" }}>
                        <div className="card-body bg-body-tertiary row justify-content-center ">
                            <div className="tab-content col-lg-5 mt-5 pt-2">
                                <div className="tab-pane preview-tab-pane active" role="tabpanel" aria-labelledby="tab-dom-a608d1fc-5a56-42e8-87bc-fbe2e0dfd054" id="dom-a608d1fc-5a56-42e8-87bc-fbe2e0dfd054">
                                    <div className="mb-3">
                                        <label className="form-label" htmlFor="exampleFormControlInput1">Current Password</label>
                                        <input className="form-control" id="exampleFormControlInput1" type="password"  />
                                    </div>
                                    <div className="mb-3">
                                        <label className="form-label" htmlFor="exampleFormControlInput1">New password</label>
                                        <input className="form-control" id="exampleFormControlInput1" type="password"  />
                                    </div>
                                    <div className="mb-3">
                                        <label className="form-label" htmlFor="exampleFormControlInput1">Re-Type New Password</label>
                                        <input className="form-control" id="exampleFormControlInput1" type="password"  />
                                    </div>

                                    <div className='text-center'>
                                        <button className='btn btn-primary'>Change password</button>
                                    </div>

                                </div>

                            </div>


                        </div>
                    </div>

                </div>

            </div>
            <Customize/>
        </main>
    )
}

export default Changepassword
